@import url(https://fonts.googleapis.com/css?family=Kanit);

.item {
  line-height: 2rem;
  display: block;
  width: 100%;
  overflow: hidden;
  padding: 10px 30px 10px 24px;
  position: relative;
  margin: 5px 0; }
  .item .menu-icon {
    top: 9px;
    left: 22px;
    position: absolute; }
    .item .menu-icon svg {
      stroke-width: 1.6;
      color: #f8f9fa; }
  .item .menu-item-text {
    display: block;
    margin-left: 40px;
    font-weight: 400;
    text-decoration: none;
    color: #f8f9fa;
    font-size: 0.95rem; }
  .item:hover {
    background-color: rgba(200, 200, 200, 0.2);
    cursor: pointer;
    text-decoration: none; }
  .item.singleItem {
    padding: 10px 30px 10px 14px; }

div[data-background-color="white"] .menu-item-text {
  color: #343a40; }

.menu-title {
  line-height: 2rem;
  margin: 5px 0;
  display: block;
  width: 100%;
  overflow: hidden;
  padding: 10px 30px 10px 14px;
  position: relative; }
  .menu-title .menu-icon {
    top: 9px;
    left: 22px;
    position: absolute; }
    .menu-title .menu-icon svg {
      stroke-width: 1.6;
      color: #f8f9fa; }
  .menu-title .menu-item-text {
    display: block;
    margin-left: 40px;
    text-decoration: none;
    color: #f8f9fa;
    font-size: 0.95rem;
    font-weight: 400;
    letter-spacing: 0.6px;
    opacity: 1;
    height: auto;
    -webkit-transition: width 0.3s cubic-bezier(0, 0, 0.2, 1);
    transition: width 0.3s cubic-bezier(0, 0, 0.2, 1); }
  .menu-title .item-arrow {
    color: #f8f9fa;
    position: absolute;
    display: inline-block;
    top: 9px;
    right: 17px; }
    .menu-title .item-arrow svg {
      -webkit-transition: all ease-in-out 0.2s;
      transition: all ease-in-out 0.2s;
      stroke-width: 1.6; }
    .menu-title .item-arrow.right-open svg {
      -webkit-transform: rotate(90deg);
              transform: rotate(90deg); }
    .menu-title .item-arrow.right-close svg {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg); }

